body {
  font-family: 'Open Sans', sans-serif; }

.logo {
  position: absolute;
  bottom: 40px;
  left: 40px; }

.coming {
  background: #000;
  width: 100%;
  height: 100vh;
  // transform: translateY(-100%)
  padding: 50px;
  &__text {
    float: right;
    text-align: right;
    perspective: 100vw;
    animation: j 2s;
    p {
      color: #ffffff;
      cursor: pointer;
      font-size: 50px;
      text-transform: uppercase;
      font-weight: 800;
      margin-bottom: 10px;
      transform: rotateY(-45deg) translateZ(0) scale(1);
      transform-origin: right center;
      transform-style: preserve-3d;
      will-change: transform;
      transition: transform 1.5s cubic-bezier(.075,.82,.165,1);
      animation: k 2s;
      a {
        text-decoration: none;
        color: inherit; }
      &:hover {
        text-stroke: 2px #fff;
        -webkit-text-stroke: 2px #fff;
        color: transparent;
        transform: rotateY(-35deg) translateZ(0) scale(1);
        a {
          text-stroke: 2px #fff;
          -webkit-text-stroke: 2px #fff;
          color: transparent; } } } } }

@keyframes j {
  0% {
    transform: translate3d(0, -110%, 0); }

  to {
    transform: translateZ(0); } }


@keyframes k {
  0% {
    visibility: visible;
    opacity: 0;
    transform: rotateY(-100deg) translateZ(0) scale(1); }

  to {
    visibility: visible;
    opacity: 1;
    transform: rotateY(-45deg) translateZ(0) scale(1); } }
