@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "iconfont";
  src: url("../fonts/iconfont.eot");
  src: url("../fonts/iconfont.eot?#iefix") format("eot"), url("../fonts/iconfont.woff2") format("woff2"), url("../fonts/iconfont.woff") format("woff"), url("../fonts/iconfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.icon {
  font-family: "iconfont";
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon {
  display: inline-block;
}

.icon-facebook:before {
  content: "Ó®Å";
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  font-family: 'Open Sans', sans-serif;
}

.logo {
  position: absolute;
  bottom: 40px;
  left: 40px;
}

.coming {
  background: #000;
  width: 100%;
  height: 100vh;
  padding: 50px;
}

.coming__text {
  float: right;
  text-align: right;
  perspective: 100vw;
  animation: j 2s;
}

.coming__text p {
  color: #ffffff;
  cursor: pointer;
  font-size: 50px;
  text-transform: uppercase;
  font-weight: 800;
  margin-bottom: 10px;
  transform: rotateY(-45deg) translateZ(0) scale(1);
  transform-origin: right center;
  transform-style: preserve-3d;
  will-change: transform;
  transition: transform 1.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  animation: k 2s;
}

.coming__text p a {
  text-decoration: none;
  color: inherit;
}

.coming__text p:hover {
  text-stroke: 2px #fff;
  -webkit-text-stroke: 2px #fff;
  color: transparent;
  transform: rotateY(-35deg) translateZ(0) scale(1);
}

.coming__text p:hover a {
  text-stroke: 2px #fff;
  -webkit-text-stroke: 2px #fff;
  color: transparent;
}

@keyframes j {
  0% {
    transform: translate3d(0, -110%, 0);
  }
  to {
    transform: translateZ(0);
  }
}

@keyframes k {
  0% {
    visibility: visible;
    opacity: 0;
    transform: rotateY(-100deg) translateZ(0) scale(1);
  }
  to {
    visibility: visible;
    opacity: 1;
    transform: rotateY(-45deg) translateZ(0) scale(1);
  }
}
